var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bgs",style:({ backgroundImage: 'url(bg/bglon2.webp),url(bg/bglon1.webp)' })},[_c('b-row',{staticClass:"fh"},[_c('b-col',{attrs:{"cols":"2"}},[_c('LeftCom')],1),_c('b-col',{attrs:{"cols":"8"}},[_c('b-row',{staticClass:"bookview"},[_c('div',{staticClass:"book"},[(_vm.kkk > 0)?_c('div',{staticClass:"pages",attrs:{"id":"pages"}},[_c('div',{staticClass:"page",class:_vm.getCl(0),style:(_vm.getZindex(0)),on:{"click":function($event){return _vm.clicks(0)}}},[_c('div',{staticClass:"book",style:({ backgroundImage: 'url(bg/a.png), url(' + _vm.anh + ')' })})]),_c('div',{staticClass:"page2",class:_vm.getCl(1),style:(_vm.getZindex(1))},[_c('Modau')],1),_c('div',{staticClass:"page scolls",class:_vm.getCl(2),style:(_vm.getZindex(2)),on:{"click":function($event){return _vm.clicks(2)}}},[_c('div',{staticClass:"box-content"},[_c('h2',{staticStyle:{"text-align":"center"}},[_c('span',{},[_vm._v("Sự tích ông bình vôi - Truyện cổ tích thế tục ")])]),_c('h2',[_vm._v("  ")]),_c('h3',[_c('span',{},[_c('em',[_vm._v("Sự tích kể về một người con gái tính tình vốn kiêu căng. Vì sự mâu thuẫn với chồng của mình mà cô bỏ đi tu rồi trở thành một tu sĩ. Trên đường đi đến đất Thánh để hỏi về con đường tu hành của mình cô đã có thêm hai người bạn đồng hành. Nhưng vì tính tình xấu xa mà đã bày mưu hãm hại hai người bạn. Sau đó, nữ tu đã bị Đức Phật trừng phạt biến thành chiếc bình vôi. ")])])])])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(3),style:(_vm.getZindex(3)),on:{"click":function($event){return _vm.clicks(3)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Ngày xưa, có một người con gái con một nhà giàu có. Cô rất đẹp nhưng cũng rất kiêu. Cô đã từng làm cho các bạn gái xa lánh mình. Cô làm cho các chàng trai ghét cô vô hạn. Cũng vì thế đến tuổi lấy chồng, cô gái vẫn chưa có đám nào vừa ý. Chàng trai nào cũng bị cô sổ toẹt, vì cao chê ngỏng, thấp chê lùn, lớn chê béo trục béo tròn, gầy chê xương sống xương sườn bày ra."),_c('br'),_c('br'),_vm._v(" Nhưng rồi cô cũng lấy được chồng. Chồng cô yêu vợ nhưng lại bực mình vì thói ghen của vợ. Cô ghen chồng làm cho xóm giềng luôn cau mặt vì những lời qua tiếng lại của họ. Cuối cùng hai người không vừa ý nhau và ly dị. Buồn bực vì duyên phận, cô bỏ đi tu."),_c('br'),_c('br'),_vm._v(" Cô xuất gia ở một ngôi chùa cổ trên núi suốt hai mươi năm. Những con chim , con thú rừng hầu như quen thuộc bóng dáng của người sư nữ. Hai mươi năm qua, cô vẫn chưa đắc đạo. Cô thắc mắc, vì tự cho mình thông kinh kệ hơn người và chịu đủ mọi sự khổ hạnh của nhà chiền. Một ngày kia cô quyết định sang Tây trúc một phen để tìm cho ra lẽ. Đường sang Tây-trúc thiên nguy vạn hiểm nhưng cô quyết định đi cho bằng được."),_c('br'),_c('br'),_vm._v(" Một hôm, sau khi vượt qua một trái núi, sư nữ tìm vào một ngôi nhà hẻo lánh dọc đường để nghỉ chân. Hai mẹ con chủ nhân tuy người rừng núi quê mùa nhưng vốn là kẻ ăn chay niệm Phật, nên thấy khách là nhà tu hành thì tiếp đãi rất hậu. Khi được nghe kể công trình tu luyện của sư nữ họ càng cung kính, coi như bậc thầy. Và khi biết rõ ý định của sư nữ thì họ cũng xin phép bỏ nhà bỏ cửa đi theo thầy để mong được đắc dạo. Nghe họ câu khẩn, sư nữ cười: – Hai mẹ con nhà này cũng muốn thành Phật ư? Được, cứ đi theo ta!. Nhưng bụng nàng bảo dạ: – “Chuông khánh còn chẳng ăn ai, nữa là mảnh chĩnh vứt ngoài bờ tre”."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Từ hôm đó nhà sư có thêm hai người bạn đồng hành. Chân bớt mỏi, đường bớt dài, họ đi chả mấy chốc đã tới đất Thánh. Từ trước đến sau, hai mẹ con nhà nọ vẫn cung kính, coi sư nữ như thầy. Còn sư nữ đối với họ không được như trước: – Không biết chừng họ được thành Phật trước ta. Họ sẽ hơn ta… Bọn này mà đắc đạo, thật là một điều nhục cho Thiền môn."),_c('br'),_c('br'),_vm._v(" Nói chuyện đức Phật khi vừa nghe tin có người tìm đường đến Tây-trúc cầu đạo, vội hóa thân đi theo dõi. Từ đầu đến cuối, đức Phật vẫn không bỏ sót một lời nói, một cử chỉ nào của sư nữ. Khi họ sắp qua một con sông rộng, đức Phật hóa phép hiện ra ở bên kia bờ một tòa cổ tự, trước cửa có một cây bồ đề rất lớn, để chờ họ."),_c('br'),_c('br'),_vm._v(" Muốn cho hai mẹ con khỏi lẽo đẽo theo mình đến đất Thánh, nên khi qua sông sư nữ giả cách ngạc nhiên, chỉ ngôi chùa và ngôi đền mà bảo rằng:"),_c('br'),_c('br'),_vm._v(" – Kìa, chúng ta đã tới Tây trúc . Chóng thật! Chính là cây bồ đề của đức Thế Tôn tu luyện ngày xưa. Thôi! Hai mẹ con cứ việc trèo lên một cành cao niệm kinh rồi buông tay rơi xuống là tức khắc thành Phật!."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Tin tưởng ở lời nói của bậc thầy, hai mẹ con mừng rỡ làm theo không chút ngần ngại. Nhưng khi họ buông tay cho người rơi xuống thì đức Phật đã đón họ đưa lên trời. Có bốn vị La hán mang tòa sen đến rước đi. Tay hai mẹ con vẫy vẫy như có ý gọi người bạn đồng hành."),_c('br'),_c('br'),_vm._v(" – “Đúng là họ thành Phật rồi!. Sư nữ vừa kinh ngạc vừa mừng, vội trèo lên cây để làm như họ và để mong được như họ. Nhưng đức Phật đã có ý trừng phạt người đàn bà kiêu ngạo và độc ác ấy một cách đích đáng, nên đã để cho cái xác rơi phịch xuống đất, tan xương vỡ sọ. Và sau đó đức Phật lại bắt con người khốn nạn ấy hóa thành bình vôi. Tại sao lại cho hóa thành bình vôi? Có người bảo chính là đức Phật muốn bắt những kẻ trong lòng bất nhân, nhưng lại đeo bộ dạng từ bi, phải để cho người đời luôn luôn móc ruột."),_c('br'),_c('br'),_vm._v(" Một truyện khác nhiều tính chất phật thoại cũng nói về sự tích ông bình vôi:"),_c('br'),_c('br'),_vm._v(" Có một anh ăn trộm từng sống bao nhiêu năm với nghề. Hắn ta không vợ con gia sản, chỉ độc có một túp lều bên cạnh đường cái. Một hôm, có cặp vợ chồng người ăn mày qua đó xin trú chân. Hắn cho họ ở lại và sau đó hắn thấy chồng bảo vợ: – “Hôm nay không xin được tí gì cả. Gần đây có một nương khoai, chờ đến tối tôi sẽ đào trộm về ăn”. Vợ can chồng: – “Thôi đi ông! Chỉ vì kiếp trước chúng ta ăn ở thất đức nên mới như thế này. Có lẽ nào phạm thêm vào cho nặng tội kiếp sau. Tôi thà chết không ăn trộm!”."),_c('br'),_c('br'),_vm._v(" Người ăn trộm nghe chuyện bỗng dưng hối hận. Cuối cùng hắn giao tất cả nhà cửa cho hai vợ chồng người ăn mày rồi bỏ di. Hắn tìm đến một ngôi chùa trên núi cao xin hòa thượng cho cạo đầu tu hành. Người ta giao cho hắn công việc nấu ăn: một bữa phải nấu từ tinh sương và một bữa từ chiều. Phận sự của hắn là giữ lửa không được để tắt, vì một lần tắt lửa thì lại phải xuống núi xin về, cách xa hàng bao nhiêu dặm."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Từ lúc nhận việc nhà chùa, hắn tỏ ra chu đáo lạ thường, chưa có bữa nào để lửa tắt. Một sư bác trên chùa ghét chú tiểu mới, bèn chờ một hôm tiểu ta ngủ say, tưới tắt lửa nhấm ở bếp."),_c('br'),_c('br'),_vm._v(" Gần sáng, chú tiểu dậy thấy không còn lửa, nghĩ đến phận sự, vội vã ba chân bốn cẳng vượt mấy khu rừng tìm xuống làng xin lửa. Giữa đường hắn gặp một con cọp già đòi ăn thịt. – “Tôi vui lòng để ngài xơi thịt – hắn nói – nhưng hãy cho tôi chạy xuống làng xin lửa cho nhà chùa, rồi tôi sẽ tới nộp mạng”."),_c('br'),_c('br'),_vm._v(" Cọp bằng lòng để cho đi. Khi đưa được lửa về, hắn kể sự tình và xin phép hòa thượng cho đi nộp mình cho cọp. Đến nơi, cọp bảo: – “Tao già rồi, răng đã rụng hết mà xương của mày cứng khó nhai. Vậy mày hãy trèo lên cây kia buông mình rơi xuống cho gẫy xương, tao mới ăn được”. Hắn vâng lời cọp, nhưng khi buông tay thì đức Phật đã đón sẵn rước lên trời, độ cho thành Phật, tức là Phật Nhiên Đăng."),_c('br'),_c('br'),_vm._v(" Vị sư bác kia thấy chú tiểu chỉ bằng vài hành động đơn giản mà được  thành chính quả thì cũng muốn được như thế. Hắn bèn xin phép hòa thượng cho mình ra đương việc nấu ăn và giữ lửa. Thế rồi một hôm hắn cũng giả tảng để cho lửa tắt, rồi cũng chạy xuống núi xin lửa. Và hắn cũng gặp cọp, cũng khất cọp xin đưa lửa về cho nhà chùa dùng rồi sau đó sẽ xin nộp mình. Nhưng đức Phật vốn hiểu rõ những việc làm của hắn xuất phát từ tấm lòng không thực, nên khi hắn trèo lên cây và buông tay, thì Phật lập tức bắt hắn hoá thành bình vôi."),_c('br'),_c('br'),_vm._v(" Một dị bản của truyện trên cũng là một phật thoại, là Sự tích cái ống nhổ."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(7),style:(_vm.getZindex(7)),on:{"click":function($event){return _vm.clicks(7)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Ở đây cũng có hai nhân vật chính là chú tiểu và sư bác. Chú tiểu mới đi tu nhưng rất chăm chỉ, biết lo lắng làm tròn chức trách. Thấy lửa ở đống nhấm tắt (kỳ thực là do sư bác ghen tỵ phun nước cho tắt), tiểu không nề hiểm nguy chạy xuống núi vào lúc mờ sáng để có lửa về kịp nấu ăn cho nhà chùa. Phật hiện ra với trạng mạo một ông già khuyên can chú tiểu đừng đi, nhưng thấy chú cương quyết đi, bèn bảo chú trèo lên cây buông tay xuống, sẽ có kẻ đưa đi an toàn. Quả nhiên chú tiểu buông tay rơi đúng trên lưng một con muông; nó đưa chú xuống làng, rồi sau khi xin được lửa lại đưa về vô sự."),_c('br'),_c('br'),_vm._v(" Sư bác vốn không phải là chân tu, nhưng thấy chú tiểu gặp Phật, cũng muốn được như vậy, bèn cũng xin ra làm công việc nấu ăn và giữ lửa, sau đó hắn đã để tắt lửa và cũng được gặp Phật . Nhưng khi buông tay thì Phật đã bắt hắn hoá thành cái ống nhổ, miệng luôn luôn há ra cho người ta khạc nhổ vào, để tỏ lòng khinh bỉ."),_c('br'),_c('br'),_vm._v(" Nguồn: Truyện cổ tích Tổng hợp. ")])]),_c('div',{staticClass:"page",class:_vm.getCl(8),style:(_vm.getZindex(8)),on:{"click":function($event){return _vm.denketthuc()}}},[_c('KetThuc')],1)]):_vm._e()])])],1),_c('b-col',{attrs:{"cols":"2"}},[_c('RightCom')],1)],1)],1)
}
